        align-items: center;
    color: #FFFFFF;
    cursor: default;
    font-size: 1.4rem;
    display: block;
    font-weight: bold;
    text-transform: uppercase;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .calendar header .month-display {
    -ms-flex-align: center;
        align-items: center;
    height: 40px;
    /* background: #2875C7; */
    background: #000;
