    color: #000;
    font-weight: bold;
    cursor: default;
    font-size: 1.2rem;
  }
  .calendar .day-names .day {
    cursor: default;
  }
  .calendar .day-names .day:hover {
    background: inherit;
  }
  .calendar .day {
    display: -ms-flexbox;
