        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1;
        flex: 1 1;
    height: 35px;
    border-left: solid 1px #CCCCCC;
    cursor: pointer;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
  }
  .calendar .day:hover {
    background: #EFEFEF;
  }
  .calendar .day:first-child {
    border-left: none;
  }
  .calendar .day.today {
    background: #dceaf8;
  }
  .calendar .day.different-month {
    color: #C0C0C0;
  }
  .calendar .day.selected {
    /* background: #2875C7; */
    background: #000;
    color: #FFFFFF;
  }
  


