.card {
  background: #fff;
  border-radius: 1px;
  height: 625px;
  margin: 1rem;
  /* position: relative; */
  width: 225px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;

  /* clear: both; */
}

.card > .img-container {
  height: 60%;
  overflow: hidden;
  text-align: center;
  background:#ccc;
}
.card > .img-container > img {
  width: 80%;
  margin-top: 20px;
  border-radius: 50%; 
  border: 4px solid yellow;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

#btn-up {
  margin: 20px auto;
}



